/**
 * EndowmentRouteContainer.js
 * The wrapper container for endowment
 *
 * @version 0.0.1
 * @author [Wilson](wilson@policypal.com)
 */
import window from 'global/window';
import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import { Helmet } from 'react-helmet';
import { connect } from 'react-redux';
import { change, reset } from 'redux-form';
import { /* Prompt, */ withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';
import Page from 'components/Page/Page';
import Loadable from 'react-loadable';

import { resetCompare } from '../../actions/compareActions';
import { getPromoGiveaways, resetInsuranceForm, FORMSTATES } from '../../actions/insuranceActions';
import { clearEndowmentData } from '../../actions/endowmentActions';
import { isProduction } from '../../lib/tagFunctions';

// import StepProgressBar from '../../components/Templates/StepProgressBar/StepProgressBar';
import RefreshTimer from '../../components/Templates/RefreshTimer/RefreshTimer';

// import EndowmentMainContainer from './EndowmentMainContainer';
// import EndowmentChoosePlanContainer from './EndowmentChoosePlanContainer';
// import EndowmentDetailContainer from './EndowmentDetailContainer';

import { routeHandler } from '../../lib/helperFunctions';

const formName = 'endowment';
const detailFormName = 'endowment-detail';

const StepProgressBar = Loadable({
  loader: () => import(/* webpackChunkName: "StepProgressBar" */ '../../components/Templates/StepProgressBar/StepProgressBar'),
  loading: () => null,
  modules: ['StepProgressBar']
});

const EndowmentMainContainer = Loadable({
  loader: () => import(/* webpackChunkName: "EndowmentMainContainer" */ './EndowmentMainContainer'),
  loading: () => null,
  modules: ['EndowmentMainContainer']
});

const EndowmentChoosePlanContainer = Loadable({
  loader: () => import(/* webpackChunkName: "EndowmentChoosePlanContainer" */ './EndowmentChoosePlanContainer'),
  loading: () => null,
  modules: ['EndowmentChoosePlanContainer']
});

const EndowmentDetailContainer = Loadable({
  loader: () => import(/* webpackChunkName: "EndowmentDetailContainer" */ './EndowmentDetailContainer'),
  loading: () => null,
  modules: ['EndowmentDetailContainer']
});

class EndowmentRouteContainer extends Component {
  static getDerivedStateFromProps(nextProps, prevState) {
    const { formIndex, isMounted } = prevState;
    const { formState, match, history } = nextProps;

    if (isMounted) {
      const routeResult = routeHandler(formName, formState, formIndex, match, history);
      if (routeResult && routeResult.formIndex !== null) {
        return ({
          formIndex: routeResult.formIndex,
        });
      }
    }
    return null;
  }

  constructor(props) {
    super(props);

    this.state = {
      formIndex: 0,
      isInitialLoad: true,
    };

    const {
      resetStoreForm,
      endowmentPromoGiveAway,
      clearData,
      history,
    } = this.props;

    // history.replace(`/endowment${history.location.search}`);

    clearData();
    this.props.resetCompare();
    this.props.resetInsuranceForm();

    resetStoreForm('endowment');
    resetStoreForm('endowment-detail');

    if (endowmentPromoGiveAway === undefined) {
      this.props.getPromoGiveaways();
    }

    this.setInitialFormState = this.setInitialFormState.bind(this);
    this.renderStepProgress = this.renderStepProgress.bind(this);
    this.renderLifeForms = this.renderLifeForms.bind(this);
    this.changeUserDetails = this.changeUserDetails.bind(this);
  }

  componentDidMount() {
    // window.addEventListener('beforeunload', this.handleLeavePage);
    this.setInitialFormState();
    this.setState({
      // eslint-disable-next-line
      isMounted: true,
    });
  }

  componentDidUpdate(prevProps) {
    const { isInitialLoad } = this.state;
    const { userDetails } = this.props;

    if (isInitialLoad && _.isEqual(userDetails, prevProps.userDetails)) {
      this.changeUserDetails();
    }
  }

  componentWillUnmount() {
    const { clearData, resetStoreForm } = this.props;
    clearData();
    this.props.resetCompare();
    this.props.resetInsuranceForm();

    resetStoreForm('endowment');
    resetStoreForm('endowment-detail');
  }

  setInitialFormState() {
    const { changeForm } = this.props;

    changeForm(formName, 'birth_date', '01 Jan 1990');
    changeForm(formName, 'premium_term', '16-20 years');

    this.changeUserDetails();
  }

  changeUserDetails() {
    const { isInitialLoad } = this.state;
    const { userDetails, changeForm } = this.props;

    if (userDetails) {
      const surname = userDetails && userDetails.surname ? userDetails.surname : '';
      const firstname = userDetails && userDetails.firstName ? ` ${userDetails.firstName}` : '';

      changeForm(detailFormName, 'email', userDetails.email);
      changeForm(detailFormName, 'name', `${surname}${firstname}`);
      changeForm(detailFormName, 'birth_date', userDetails.dateOfBirth);
      changeForm(detailFormName, 'mobile_number', userDetails.contact);

      if (isInitialLoad) {
        this.setState({
          isInitialLoad: false,
        });
      }
    }
  }

  renderStepProgress() {
    const { formIndex } = this.state;
    return (
      <StepProgressBar
        maxWidth="556px"
        formState={formIndex}
        inputList={[
          'Choose Plan',
          'Details',
        ]}
        form="endowment"
        stateList={[
          FORMSTATES.CHOOSEPLAN,
          FORMSTATES.DETAILFORM,
        ]}
        stateDescriptions={[
          { main: '' /* 'Choose your Plan', sub: 'Compare price plans from multiple companies' */},
          { main: 'Fill in your details - Get $5 Credits' },
        ]}
        charged={false}
      />
    );
  }

  renderLifeForms() {
    const { formIndex } = this.state;
    const {
      userDetails,
    } = this.props;

    const isLoggedIn = !_.isEmpty(userDetails);


    switch (formIndex) {
      case FORMSTATES.CHOOSEPLAN.index:
        return <EndowmentChoosePlanContainer isLoggedIn={isLoggedIn} />;
      case FORMSTATES.DETAILFORM.index:
        return (
          <EndowmentDetailContainer
            isLoggedIn={isLoggedIn}
            piggyCallback={this.changeUserDetails}
          />
        );
      default:
        return <EndowmentMainContainer isLoggedIn={isLoggedIn} />;
    }
  }

  render() {
    const { endowmentPromoGiveAway, constants } = this.props;

    if (!constants || endowmentPromoGiveAway === undefined) {
      return null;
    }

    const meta = isProduction ? 'PolicyPal, insurance, insurance broker, endowment, endowment plan, savings plan singapore, best endowment plan singapore, 5 years saving plan singapore, endowment fund, endowment insurance, what is endowment plan, best insurance savings plan singapore, endowment policy, ntuc endowment, short term endowment plan singapore, short term endowment plan, best endowment plan, ntuc income endowment, endowment plan comparison, singapore endowment insurance, savings insurance, savings insurance singapore, compare insurance' : '';
    return (
      <Page id="endowment">
        <Helmet>
          <meta name="keywords" content={meta} />
          <title>PolicyPal 2.0 - Endowment Insurance</title>
          <meta name="title" content="PolicyPal 2.0 - Endowment Insurance" />
          <meta name="description" content="Browse products for endowment insurance." />
        </Helmet>
        {/* {
          formState !== FORMSTATES.MAIN ?
            <Prompt
              when={true}
              message="Are you sure you want to leave current page?"
            />
          :
            null
        } */}
        {this.renderStepProgress()}
        {this.renderLifeForms()}
        <RefreshTimer ref={(node) => {
            if (!this.refresher) {
              node.invokeNewTimer();
            }
            this.refresher = node;
          }}
        />
      </Page>
    );
  }
}

EndowmentRouteContainer.defaultProps = {
  userDetails: null,
  endowmentPromoGiveAway: undefined,
  constants: null,
};

EndowmentRouteContainer.propTypes = {
  clearData: PropTypes.func.isRequired,
  resetCompare: PropTypes.func.isRequired,
  getPromoGiveaways: PropTypes.func.isRequired,
  resetInsuranceForm: PropTypes.func.isRequired,
  changeForm: PropTypes.func.isRequired,
  resetStoreForm: PropTypes.func.isRequired,
  userDetails: PropTypes.object,
  endowmentPromoGiveAway: PropTypes.object,
  constants: PropTypes.object,
  history: PropTypes.object.isRequired,
};

const mapStateToProps = state => ({
  endowmentPromoGiveAway: state.insurance.promoGiveaways.endowment,
  formState: state.insurance.formState,
  constants: state.insurance.constants,
  userDetails: state.userDetails.data,
});

const mapDispatchToProps = dispatch => ({
  changeForm: (form, key, value) => (
    dispatch(change(form, key, value))
  ),
  resetInsuranceForm: () => (
    dispatch(resetInsuranceForm())
  ),
  resetStoreForm: form => (
    dispatch(reset(form))
  ),
  getPromoGiveaways: () => {
    dispatch(getPromoGiveaways('endowment'));
  },
  resetCompare: () => {
    dispatch(resetCompare());
  },
  clearData: () => {
    dispatch(clearEndowmentData());
  },
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(EndowmentRouteContainer));
