/**
 * PopupWithText.js
 * Popup with custom text
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';

import Button from 'components/FormsV3/Button/Button';
import Popup from '../Popup/Popup';


import './PopupWithText.scss';

class PopupWithText extends Component {
  constructor(props) {
    super(props);

    const { onConfirm } = this.props;

    this.state = {
      text: '',
      jsx: null,
      useConfirm: true,
      onConfirm,
    };
    this.openPopupWithText = this.openPopupWithText.bind(this);
  }

  openPopupWithText(text, useConfirm = true, onConfirm = null) {
    this.setState({
      text,
      useConfirm,
    });

    if (onConfirm) {
      this.setState({
        onConfirm,
      });
    }
    setTimeout(this.popupRef.openPopup, 10);
  }

  openPopupWithJSX(jsx, useConfirm = true, onConfirm = null) {
    this.setState({
      jsx,
      useConfirm,
    });

    if (onConfirm) {
      this.setState({
        onConfirm,
      });
    }
    setTimeout(this.popupRef.openPopup, 10);
  }

  render() {
    const {
      text, jsx, useConfirm, onConfirm,
    } = this.state;
    const { showCancelButton, hideCloseButton } = this.props;
    return (
      <div className="popup-with-text">
        <Popup
          hideCloseButton={hideCloseButton}
          ref={(r) => { this.popupRef = r; }}
        >
          <Fragment>
            <div className="check-out-popup">
              <div className="upper-container upper-margin-down">
                {
                  jsx || (
                    <p>
                      <span className="focus-text">{text}</span>
                    </p>
                  )
                }
              </div>
              <div className="button-container">
                {
                  showCancelButton ?
                    <Button
                      className="checkout-button"
                      variant="medium-flat"
                      width={166}
                      height={72}
                      onClick={() => {
                        this.popupRef.closePopup();
                      }}
                    >
                      <p>Cancel</p>
                    </Button>
                  :
                    null
                }
                <Button
                  className="checkout-button"
                  variant="medium-flat-green"
                  width={166}
                  height={72}
                  onClick={() => {
                    if (useConfirm) {
                      onConfirm();
                    }
                    this.popupRef.closePopup();
                  }}
                >
                  <p>Confirm</p>
                </Button>
              </div>
            </div>
          </Fragment>
        </Popup>
      </div>
    );
  }
}

PopupWithText.defaultProps = {
  onConfirm: () => {},
  showCancelButton: false,
  hideCloseButton: false,
};

PopupWithText.propTypes = {
  onConfirm: PropTypes.func,
  showCancelButton: PropTypes.bool,
  hideCloseButton: PropTypes.bool,
};

export default PopupWithText;
